    <div class="row">
      <div class="col-md-12" style="text-align: left">
        <!-- <Info :message="description"/> -->
        <div class="card" v-if="user.Name.includes(':aggregator')">
          <div class="card-header">
            <b-button v-b-toggle.collapse-1 variant="link">Add Procurment</b-button>
          </div>
          <b-collapse id="collapse-1" class="mt-2">
            <div class="card-body">
              <div class="row">
                 <div class="col-md-6">
                  <div class="form-group">
                    <label style="margin-right: 8%">Vehicle Number:</label>
                    <input
                      type="text"
                      v-model="vehiclNumber"
                      size="30"
                      placeholder="Enter vehicle number"
                      class="form-control"
                    />
                  </div>
                </div>
                <div class="col-md-6">
                  <div class="form-group">
                    <label style="margin-right: 8%">Type Of Material:</label><br/>
                    <input
                      type="text"
                      v-model="typOfMaterial"
                      size="30"
                      placeholder="Enter type of material"
                      class="form-control"
                    />
                  </div>
                </div>
              </div>
              <div class="row">
                <div class="col-md-6">
                  <div class="form-group">
                    <label style="margin-right: 8%">Number Of Sacks:</label>
                    <input
                      type="text"
                      v-model="numprOfSacks"
                      size="30"
                      placeholder="Enter number of sacks"
                      class="form-control"
                    />
                  </div>
                </div>
                <div class="col-md-6">
                  <div class="form-group">
                    <label style="margin-right: 8%">Total weight of material shipped:</label>
                    <input
                      type="text"
                      v-model="weights"
                      size="30"
                      placeholder="Enter weight"
                      class="form-control"
                    />
                  </div>
                </div>
              </div>
             
              <div class="row">
                <div class="col-md-12">
                  <hr/>
                  <button class="btn btn-outline-primary btn-sm" @click="createProcurment()">Create</button>
                </div>
              </div>
            </div>
          </b-collapse>
        </div>
        <div class="card" v-if="user.Name.includes(':inward')">
          <div class="card-header">
            <b-button v-b-toggle.collapse-1 variant="link">Update Procurment</b-button>
          </div>
          <b-collapse id="collapse-1" class="mt-2">
            <div class="card-body">
              <div class="row">
                <div class="col-md-6">
                  <div class="form-group">
                    <label style="margin-right: 8%">Procurement Id:</label>
                    <input
                      type="text"
                      v-model="procurmentId"
                      size="30"
                      placeholder="Enter proccurment id"
                      class="form-control"
                    />
                  </div>
                </div>
                <div class="col-md-6">
                  <div class="form-group">
                    <label style="margin-right: 8%">Grn Number:</label>
                    <input
                      type="text"
                      v-model="grn_number"
                      size="30"
                      placeholder="Enter proccurment id"
                      class="form-control"
                    />
                  </div>
                </div>
              </div>
                <div class="row">
                  <div class="col-md-6">
                  <div class="form-group">
                    <label style="margin-right: 8%">STATE:</label>
                    <b-form-select
                        v-model="selected"
                        :options="selectOptions"
                        @change="OnSchemaSelectDropDownChange($event)"
                        size="md"
                        class="form-control"
                      ></b-form-select>
                  </div>
                  </div>
                  <div class="col-md-6">
                  <div class="form-group">
                    <label style="margin-right: 8%">Comment:</label>
                    <input
                      type="text"
