                      class="form-control"
                    />
                  </div>
                </div>
                </div>
              <div class="row">
                <div class="col-md-12">
                  <hr/>
                  <button class="btn btn-outline-primary btn-sm" @click="acceptProcurment()">Accept</button>
                </div>
              </div>
            </div>
          </b-collapse>
        </div>
      </div>
    </div>
    <div class="row" style="margin-top: 2%;">
      <div class="col-md-12">        
        <table class="table table-bordered" style="background:#FFFF">
          <thead class="thead-light">
            <tr>
              <th>Procurement Id</th>
              <th>Owner</th>
              <th>State</th>
              <th v-if="user.Name.includes(':inward')">GRN</th>
              <th>Vehicle Number</th>
              <th>Type Of Material</th>
              <th>Number Of Sacks</th>
              <th>Weight</th>
              <th>Comment</th>
            </tr>
          </thead>

          <tbody>
            <tr v-for="row in schemaList" :key="row">
              <th>
                  {{row.procurementId}}
              </th>
              <td>
                <a :href="`${$config.nodeServer.BASE_URL}${$config.nodeServer.DID_RESOLVE_EP}/`+row.id" target="_blank">{{row.id}}</a>
              </td>
              <!-- <td>{{row.attributes}}</td> -->
              <td
                style="word-wrap: break-word;min-width: 200px;max-width: 200px;"
              >{{row.state}}</td>
              <td
                style="word-wrap: break-word;min-width: 200px;max-width: 200px;"
                v-if="user.Name.includes(':inward')"
              >{{row.grn_number}}</td>
              <td>{{row.vehicleNumber}}</td>
              <td>{{row.typeOfMaterial}}</td>
              <td>{{row.numperOfSacks}}</td>
              <td>{{row.weight}}</td>
              <td>{{row.comment}}</td>
            </tr>
          </tbody>
        </table>
        <!-- </div> -->
        <!-- </div> -->
      </div>
      <!-- </div> -->
    </div>
  </div>
</template>

<script>
import fetch from "node-fetch";
import QrcodeVue from "qrcode.vue";
import Info from '@/components/Info.vue'
export default {
  name: "IssueCredential",
  components: { QrcodeVue, Info },
  data() {
    return {
      description: "Credential Schema defines what information will go inside a verifiable credential. For example: Directorate General of Civil Aviation (DGCA) can define a schema (or format) for flights tickets, being issued by all airline companies in India.",
      active: 0,
      selectOptions: [{ value: 0, text: "Accepted" }],
      host: location.hostname,
      user: {},
      prevRoute: null,
      attributeName: "",
      attributes: [],
      issueCredAttributes: [],
      showSchema: true,
      radioSelected: "create",
      procurmentId: "",
      grn_number: "",
      vehiclNumber: "",
      typOfMaterial: "",
      numprOfSacks: "",
      weights: "",
      comments: "",
      isCredentialIssued: false,
      signedVerifiableCredential: "",
      credentials: JSON.parse(localStorage.getItem("credentials")),
      subjectDid: "did:hs:AmitKumar",
      radioOptions: [
        { text: "Create new schema", value: "create" },
        { text: "Select existing schema", value: "existing" },
      ],
      selected: null,
      state:null,
      attributeValues: {},
      authToken: localStorage.getItem("authToken"),
      selectOptions: [
        { value: null, text: "Please select a schema" },
        { value: 1, text: "Accept" },
        { value: 0, text: "Reject" },
        ],
      schemaMap: {},
      schemaList: [],
      credentialDescription: "",
      fullPage: true,
      isLoading: false
    };
  },
  created() {
    const usrStr = localStorage.getItem("user");
    this.user = JSON.parse(usrStr);
    this.fetchProcurment();
  },
  beforeRouteEnter(to, from, next) {
    next((vm) => {
      vm.prevRoute = from;
    });
  },
  methods: {
    notifySuccess(msg){
        this.$notify({
          group: 'foo',
          title: 'Information',
          type: 'success',
          text: msg
        });
    },
    notifyErr(msg){
      this.$notify({
          group: 'foo',
          title: 'Error',
          type: 'error',
          text: msg
        });
    },
    fetchProcurment() {
      const url = `${this.$config.studioServer.BASE_URL}${this.$config.studioServer.PROCURMENT_LIST}`;
      fetch(url)
        .then((res) => res.json())
        .then((j) => {
          console.log(j)
          // if (j.status != 200) throw new Error(j.error);
          this.schemaList = j;
          if (this.schemaList && this.schemaList.length > 0) {
            this.schemaList = this.schemaList.filter(
              (x) => {
                let strArray = x.owner.split("#")
                x.id = strArray[1]
                return x
              }
            );
          }
        })
        .catch((e) => this.notifyErr(`Error: ${e.message}`));
    },
    fetchData(url, option) {
      fetch(url)
        .then((res) => res.json())
        .then((j) => {
          if (j.status != 200) throw new Error(j.error);
          return j.message;
        })
        .catch((e) => this.notifyErr(`Error: ${e.message}`));
    },
    gotosubpage: (id) => {
      this.$router.push(`${id}`);
    },
    addBlankAttrBox() {
      if (this.attributeName != " ") {
        this.attributes.push(this.attributeName);
        this.attributeName = " ";
      }
    },
    onSchemaOptionChange(event) {
      this.attributes = [];
      this.issueCredAttributes = [];
      this.selected = null;
      this.credentialName = "";
    },
    OnSchemaSelectDropDownChange(event) {
      if (event && event==1) {
        this.state = 'ACCEPTED'
      } else {
        this.state = 'REJECTED'
      }
    },
    forceFileDownload(data, fileName) {
      const url = window.URL.createObjectURL(new Blob([data]));
      const link = document.createElement("a");
      link.href = url;
      link.setAttribute("download", fileName);
      document.body.appendChild(link);
      link.click();
    },
    downloadCredentials() {
      this.forceFileDownload(
        JSON.stringify(this.signedVerifiableCredential),
        "vc.json"
      );
    },
    createProcurment() {
      this.isLoading = true;
      if (this.vehiclNumber == "")
      return this.notifyErr("Error: Vehicle Number can not be blank");
      if (this.typOfMaterial == "")
      return this.notifyErr("Error: typeOfMaterial can not be blank");
      if (this.numprOfSacks == "")
      return this.notifyErr("Error: Number Of Sacks can not be blank");
      if (this.weights == "")
      return this.notifyErr("Error: weight can not be blank");
      const url = `${this.$config.studioServer.BASE_URL}${this.$config.studioServer.PROCURMENT_LIST}`;
      const data = {
        vehicleNumber: this.vehiclNumber,
        typeOfMaterial: this.typOfMaterial,
        numperOfSacks: this.numprOfSacks,
        weight: this.weights,
        aggregator: this.user.id,
      };
      let headers = {
        "Content-Type": "application/json",
      };
      fetch(url, {
        method: "POST",
        body: JSON.stringify(data),
        headers,
      })
        .then((res) => res.json())
        .then((j) => {
          this.notifySuccess("Procurment successfull created");
          this.fetchProcurment()
          this.clear()
          this.isLoading = false;
        })
         .catch(e => {
          this.isLoading = false;
          console.log(e);
        });
    },
    clear(){
      this.vehiclNumber = "";
      this.typOfMaterial = "";
      this.numprOfSacks = "";
      this.weights = "";
      this.grn_number = "";
      this.comments = "";
      this.procurmentId = "";

    },
    acceptProcurment() {
      this.isLoading = true
      if (this.procurmentId == "")
        return this.notifyErr("Error: procurmentId can not be blank");
      if (this.grn_number == "")
        return this.notifyErr("Error: grn_number can not be blank");

      const url = `${this.$config.studioServer.BASE_URL}${this.$config.studioServer.ACCEPT_PROCURMENT}`;
      const data = {
        procurementId: this.procurmentId.trim(),
        inward: this.user.id,
        grn_number: this.grn_number,
        state: this.state,
        comments: this.comments
      };
      let headers = {
        "Content-Type": "application/json",
      };
      fetch(url, {
        method: "POST",
        body: JSON.stringify(data),
        headers,
      })
        .then((res) => res.json())
        .then((j) => {
          this.notifySuccess("Inward successfull accepted");
          this.clear();
          this.fetchProcurment();
          this.isLoading = false;
        })
        .catch(e => {
          this.isLoading = false;
          console.log(e);
        });
    },
  },
};
</script>


